<template>
  <div class="hello container bg-light">
    <div class="row">
      <div class="col">
        <h3>Chat Here</h3>
      </div>
    </div>
    <div class="row">
      <div class="col">

        <form @submit.prevent @submit="setUsername">
  <div class="form-group">
    <label for="exampleInputEmail1">UserName</label>
    <input type="username" class="form-control" id="usernamevalue" required placeholder="Enter username">
    <small id="emailHelp" class="form-text text-muted">You cannot change username once set</small>
  </div>

  
  <button type="submit" class="btn btn-primary">Done</button>
</form>
       
       
      </div>
    </div>
    <div class="row">
      <div class="container bg-light mt-4  displayBox" id="displayBoxID" >

   <div class="messages" v-for="(msg, index) in messages" :key="index" >
                  <p >
                    <span class="font-weight-bold">
                      {{ msg.user }}: </span>{{ msg.message }}
                      </p>
               
              </div>

      </div>
    </div>
    <div class="row">
      <div class="col">
       
         <form @submit.prevent @submit="sendMessage">
  <div class="form-group">
    <label for="Textarea1">Type and send</label>
    <textarea class="form-control" id="Textarea1" rows="3" @input="inputclick" v-model="usermessage" required maxlength="100"></textarea>
    <button type="submit" class="btn btn-primary">Send</button>
    
  </div>
</form>
              
      
          
      
      
          
      
      </div>
    </div>
    

  </div>
</template>

<script>


import io from 'socket.io-client'
export default {
  name: 'HelloWorld',
  data(){
    return{
      userName:null,
      usermessage:null,
      messages: [],
      othermessages:[],
      userId:null,
      socket : io(`${process.env.VUE_APP_URL}`)
    }
  },
  
  
     mounted() {
       this.socket.on('connectedSuccessfully',(data)=>{
        
        var datas= {user: data.userName,
                message: "is connected"}
                 this.messages.push(datas);
       })
       


       this.socket.on('userDisconnection',(data)=>{
 
        var datas= {user: data.userName,
                message: "is disconnected"}
                 this.messages.push(datas);
       })



       this.socket.on("new_msg", function(data) {
    this.userId=data;
    
    

})
       this.socket.on('userId',(data)=>{
         
         this.userId=data;
       })


        this.socket.on('messagetoall', (data) => {
            this.messages = [...this.messages, data];
          
            // you can also do this.messages.push(data)
        });
    },
  methods: {
     sendMessage(e) {
    
            e.preventDefault();
           
              this.socket.emit('messagetoall', {
                user: this.userName,
                message: this.usermessage
            });
            this.usermessage = ''
        },
    inputclick(){

        if(!this.userName){
          alert("userName is required!!");
          this.usermessage=null;
          return ;

        }
    },
    setUsername(){
     if(!this.userName){
        this.userName=document.getElementById('usernamevalue').value;
      document.getElementById('usernamevalue').disabled=true;
      this.socket.emit('userJoined',{userName:this.userName})
      
      this.socket.emit('join', {username:this.userName});
     }

    }
  },
}
</script>

